

const vParallax = {

    // called when the bound element's parent component
    // and all its children are mounted.
    mounted(el, binding) {
        console.log('parallax mounted')

        let offsetX = 0
        let offsetY = 0

        function _update(event) {
            el.style.transform = `translate(0, 0px)`
            const rect = el.getBoundingClientRect()

            const y = Math.round(-(rect.y - rect.height / 2) * binding.value / 20)

            // if (event.type == "mousemove") {
            //     offsetX = - event.screenX / innerWidth * binding.value * 20
            //     offsetY = -event.screenY / innerHeight * binding.value * 20
            //     if (event.sourceCapabilities)
            //         if (event.sourceCapabilities.firesTouchEvents) {
            //             offsetX = 0
            //             offsetY = 0
            //         }

            // }
            el.style.transform = `translate(${offsetX}px, ${y + offsetY}px)`
        }

        let block = false
        function update(event) {
            if (block) return
            block = true
            _update(event)
            requestAnimationFrame(() => block = false)
        }

        el.update = update

        update()
        addEventListener("scroll", update)
        // addEventListener("mousemove", update)
    },

    unmounted(el) {
        removeEventListener("scroll", el.update)
        // removeEventListener("mousemove", el.update)
    }
}

export default vParallax