<script setup>
import news from './news'
import HorizontalScroll from './HorizontalScroll.vue'
function dateString(date) {
    return [
        date.getFullYear(),
        date.getMonth().toString().padStart(2, "0"),
        date.getDate().toString().padStart(2, "0"),
    ].join('.')
}
</script>

<template>
    <div class="news_page">
        <div class="layout-content">
            <div class="news_page-row">
                <div class="news_page-title">
                    <h2>News</h2>
                </div>
                <div class="news_page-content">
                    <HorizontalScroll>
                        <div class="row">
                            <template v-for="item, i in news" :key="i">
                                <div class="news_page-item">
                                    <div class="news_page-item_cover" :style="{
                                        'background-image': 'url(' + item.cover_img + ')'
                                    }">
                                    </div>
                                    <div class="news_page-item_info">
                                        <h3>
                                            {{ item.title }}
                                        </h3>
                                        <span>
                                            {{ dateString(item.date) }} / {{ item.type.toUpperCase() }}
                                        </span>
                                    </div>
                                </div>
                            </template>
                        </div>
                    </HorizontalScroll>
                </div>
            </div>
        </div>
    </div>
</template>