import { ref, onMounted, onUnmounted } from 'vue'

export function useScroll() {
    const scrollY = ref(0)

    function scroll() {
        scrollY.value = window.scrollY
    }

    onMounted(function () {
        addEventListener('scroll', scroll)
    })

    onUnmounted(function () {
        removeEventListener('scroll', scroll)
    })

    return { scrollY }
}