<script setup>
import { onMounted, onUnmounted, ref, defineProps } from 'vue'
import { useScroll } from './useScroll'

const { scrollY } = useScroll()
console.log(scrollY)
const transition = ref(false)
const props = defineProps({ anchors: Array })

const isNavExpanded = ref(false)
function toggle() {
    isNavExpanded.value = !isNavExpanded.value
}

function navto(tag) {
    transition.value = true
    setTimeout(() => {
        document.querySelector(tag).scrollIntoView()
        transition.value = false
        isNavExpanded.value = false
    }, 100)
}

const breakpoints = ref({})

function update() {
    const origin = document.querySelector('#app').getBoundingClientRect().y
    props.anchors.map((x, i) => {
        breakpoints.value[i] = document.querySelector(x.tag).getBoundingClientRect().y - origin
    })
}

function isActive(i) {
    if (breakpoints.value[i] == undefined) return false
    const y = scrollY.value + 60
    if (y < breakpoints.value[i]) return false
    if (breakpoints.value[i + 1] !== undefined && y > breakpoints.value[i + 1]) return false
    return true
}

onMounted(function () {
    update()
    addEventListener('resize', update)
})

onUnmounted(function () {
    removeEventListener('resize', update)
})

</script>
<template>
    <div class="nav_bar" :class="{
        'nav_bar-invert': scrollY < 10,
        'nav_bar-float': scrollY > 700,
        'expanded': isNavExpanded,
    }">
        <div class="layout-container">
            <div class="nav_bar-items">
                <div class="nav_bar-item nav_bar-item_logo" @click="navto('#home')">
                    <div class="nav_bar-content">
                        <svg width="100%" height="100%" viewBox="0 0 5909 1134" version="1.1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" xml:space="preserve" xmlns:serif="http://www.serif.com/" style="fill-rule:evenodd;clip-rule:evenodd;stroke-linejoin:round;stroke-miterlimit:2;">
                            <g>
                                <path d="M2526.17,1129.17l-739.834,-0c-45.375,-0 -82.166,-36.792 -82.166,-82.167l-0,-964.833c-0,-45.375 36.791,-82.167 82.166,-82.167l739.834,-0c45.375,-0 82.166,36.792 82.166,82.167l0,964.833c0,45.375 -36.791,82.167 -82.166,82.167Z" style="fill-rule:nonzero;" />
                                <path d="M3561.58,1129.17l-739.833,-0c-45.375,-0 -82.167,-36.792 -82.167,-82.167l0,-964.833c0,-45.375 36.792,-82.167 82.167,-82.167l739.833,-0c45.375,-0 82.167,36.792 82.167,82.167l-0,964.833c-0,45.375 -36.792,82.167 -82.167,82.167Z" style="fill-rule:nonzero;" />
                                <path d="M4597,1129.17l-739.833,-0c-45.375,-0 -82.167,-36.792 -82.167,-82.167l-0,-964.833c-0,-45.375 36.792,-82.167 82.167,-82.167l739.833,-0c45.375,-0 82.167,36.792 82.167,82.167l-0,964.833c-0,45.375 -36.792,82.167 -82.167,82.167Z" style="fill-rule:nonzero;" />
                                <circle cx="5345.83" cy="566.667" r="562.5" />
                                <path d="M1480.33,0.917l-544,-0c-45.375,-0 -82.166,36.791 -82.166,82.166l-0,237.959c-0,40.25 -32.667,72.916 -72.917,72.916c-40.25,0 -72.917,-32.666 -72.917,-72.916l0,-237.959c0,-45.375 -36.791,-82.166 -82.166,-82.166l-544,-0c-45.375,-0 -82.167,36.791 -82.167,82.166l-0,964.834c-0,45.375 36.792,82.166 82.167,82.166l544,0c45.375,0 82.166,-36.791 82.166,-82.166l0,-237.959c0,-40.291 32.667,-72.916 72.917,-72.916c40.25,-0 72.917,32.625 72.917,72.916l-0,237.959c-0,45.375 36.791,82.166 82.166,82.166l544,0c45.375,0 82.167,-36.791 82.167,-82.166l-0,-964.792c-0,-45.417 -36.792,-82.208 -82.167,-82.208Z" style="fill-rule:nonzero;" />
                            </g>
                        </svg>
                    </div>
                </div>
                <div class="nav_bar-spacer"></div>

                <div class="nav_bar-item nav_bar-item_circle" @click="toggle()">
                </div>

                <template v-for="anchor, i in anchors" :key="anchor.tag">
                    <div class="nav_bar-item nav_bar-item_button" @click="navto(anchor.tag)" :class="{ active: isActive(i) }">
                        <div class="nav_bar-content">{{
                                anchor.text
                        }}</div>
                    </div>
                </template>
            </div>
        </div>
        <div class="nav_bar-cover" :class="{ show: transition }">

        </div>
    </div>
</template>
