<script setup>
import { onMounted, ref } from 'vue'
import SymbolHey from './SymbolHey.vue'
// Process -----------------------------------------
const defaultImg = require("@/assets/imgs/Charac_2.webp")
const processes = ref([[
    {
        step: 1, name: "Modeling",
        img: require("@/assets/imgs/Charac_2_1.webp")
    },
    { node: "1st Fix" },
    {
        step: 2, name: "UV",
        img: require("@/assets/imgs/Charac_2_2.webp")
    }
], [
    {
        step: 3, name: "Texture",
        img: require("@/assets/imgs/Charac_2_3.webp")
    },
    { node: "2nd Fix" },
    {
        step: 4, name: "Rigging",
        img: require("@/assets/imgs/Charac_2_4.webp")
    }
], [
    {
        step: 5, name: "Export",
        img: require("@/assets/imgs/Charac_2_5.webp")
    },
    { node: "3rd Fix" },
    {
        step: 6, name: "Acceptance",
        img: defaultImg
    }
]])

// Preload all images
onMounted(function () {
    for (const process of processes.value) {
        for (const subprocess of process) {
            if (subprocess.img) {
                const image = new Image()
                image.src = subprocess.img
            }
        }
    }
})

const selectedProcess = ref(processes.value[2][2])

const selectImg = ref(defaultImg)
const previousImg = ref(null)
const showImg = ref(true)


function selectProcess(process) {
    showImg.value = false

    previousImg.value = selectImg.value
    selectImg.value = process.img ? process.img : defaultImg

    setTimeout(() => {
        selectedProcess.value = process

        showImg.value = true
    }, 0)
}

// FAQ ------------------------------------------------------------
const faqs = ref([
    {
        q: "When can I expect a reply?",
        a: "We will aim to reply to your letter within two to three days."
    }, {
        q: "What information do I need to provide to create a 3D model?",
        a: "To create a 3D model, you can provide us with your footage and send it to our email address."
    }, {
        q: "How can I do business with HiiioCG?",
        a: "To do business with HiiioCG, please contact us using our email address, including the outline of your consultation or proposal, the name of your company, the person in charge."
    }, {
        q: "How can I learn more about the products offered by HiiioCG?",
        a: "You can view the products offered by HiiioCG on this page. If you have any questions about a specific product, please send us an email."
    }
])

const selectedFaq = ref(null)


function selectFaq(faq) {
    selectedFaq.value = faq
}

onMounted(function () {
    selectFaq(faqs.value[0])
})

</script>

<template>
    <div class="our_process_page">
        <div class="layout-content">
            <h2>Say Hi, <br /> To Your Character. 
                <SymbolHey></SymbolHey>
            </h2>
            <p>HIIIO extends the potential of 2D pictures with the power of 3D technologies.
                Our innovative modeling and hand-crafted cell shading techniques
                recreate the original illustration in the virtual world with the highest fidelity.</p>
            <div class="our_process_page-row">
                <div class="our_process_page-info">
                    <div class="process layout-indent">
                        <h3>Our<br />Process</h3>
                        <br>
                        <div class="process-table">
                            <div class="row" v-for="process, i in processes" :key="i">
                                <div class="process-item" :class="{ 'process-item_grow': subprocess.node }" v-for="subprocess, j in process" :key="j">
                                    <template v-if="subprocess.step">
                                        <div class="process-step" @click="selectProcess(subprocess)">{{ subprocess.step }}</div>
                                        <div class="process-name">{{ subprocess.name }}</div>
                                    </template>
                                    <template v-if="subprocess.node">
                                        <div class="process-line">
                                            <div class="process-node"> {{ subprocess.node }} </div>
                                        </div>
                                    </template>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="our_process_page-chara">
                    <div class="wrapper" v-parallax="1">
                        <img class="img-previous" v-if="previousImg" :src="previousImg" :class="{
                            'show': showImg
                        }" alt="">
                        <img class="img-current" :src="selectImg" :class="{
                            'show': showImg
                        }" alt="">
                    </div>

                    <div class="our_process_page-H" v-parallax="4">
                        <svg width="100%" height="100%" viewBox="-5 -5 1573 1144" xmlns="http://www.w3.org/2000/svg">
                            <path d="M1480.33,0.917l-544,-0c-45.375,-0 -82.166,36.791 -82.166,82.166l-0,237.959c-0,40.25 -32.667,72.916 -72.917,72.916c-40.25,0 -72.917,-32.666 -72.917,-72.916l0,-237.959c0,-45.375 -36.791,-82.166 -82.166,-82.166l-544,-0c-45.375,-0 -82.167,36.791 -82.167,82.166l-0,964.834c-0,45.375 36.792,82.166 82.167,82.166l544,0c45.375,0 82.166,-36.791 82.166,-82.166l0,-237.959c0,-40.291 32.667,-72.916 72.917,-72.916c40.25,-0 72.917,32.625 72.917,72.916l-0,237.959c-0,45.375 36.791,82.166 82.166,82.166l544,0c45.375,0 82.167,-36.791 82.167,-82.166l-0,-964.792c-0,-45.417 -36.792,-82.208 -82.167,-82.208Z" />
                        </svg>
                    </div>
                </div>
            </div>

            <div class="faqs layout-indent">
                <h3>FAQs</h3>
                <div class="row">
                    <div class="faqs-question">
                        <div class="faqs-table">
                            <div v-for="faq, i in faqs" class="faqs-item" :class="{
                                'faqs-selected': faq == selectedFaq
                            }" :key="i" @click="selectFaq(faq)">
                                <div class="faqs-block">Q{{ i + 1 }}</div>
                                <div class="faqs-q">
                                    <p>
                                        {{ faq.q }}
                                    </p>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="faqs-answer">
                        <p class="faqs-answer_A">A</p>
                        <p v-if="selectedFaq">
                            {{ selectedFaq.a }}
                        </p>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>