<script setup>
import { onBeforeUnmount, onMounted, ref } from 'vue'

const scroll = ref(null)
const at_start = ref(false)
const at_end = ref(false)

const isMouseDown = ref(false)
const scrollMovement = ref(1)
const scrollFloatCarry = ref(0)

function animation() {
    if (scroll.value == null) return
    requestAnimationFrame(animation)
    if (isMouseDown.value) return
    const scrollBy = scrollMovement.value + scrollFloatCarry.value
    const scrollByInt = Math.round(scrollBy)
    scrollFloatCarry.value = scrollBy - scrollByInt
    scroll.value.scrollBy(scrollByInt, 0)
    scrollMovement.value = scrollMovement.value * 0.9
}

function update() {
    const scrollEnd = scroll.value.scrollWidth - scroll.value.clientWidth
    const scrollLeft = scroll.value.scrollLeft
    // console.log(scrollLeft, scrollEnd)
    at_start.value = scrollLeft < 1
    at_end.value = scrollLeft > scrollEnd - 1
}


function mousedown(event) {
    console.log(event)
    if (event.button == 0 && event.target.tagName == 'DIV')
        isMouseDown.value = true
}

function mouseup() {
    isMouseDown.value = false

}

function mousemove(event) {
    if (isMouseDown.value) {
        event.preventDefault()
        scrollMovement.value = -event.movementX
        scroll.value.scrollBy(-event.movementX, 0)
    }
}

function mousewheel(event) {
    if (!(at_start.value && event.deltaY < 0 ||
        at_end.value && event.deltaY > 0)) {
        scrollMovement.value += event.deltaY / 10
        event.preventDefault()
    }
}

onMounted(function () {
    update()
    animation()
    scroll.value.addEventListener("scroll", update)
    scroll.value.addEventListener("mousedown", mousedown)
    scroll.value.addEventListener("mousewheel", mousewheel)
    addEventListener("mouseup", mouseup)
    scroll.value.addEventListener("mousemove", mousemove)
    addEventListener("resize", update)
})

onBeforeUnmount(function () {
    scroll.value.removeEventListener("scroll", update)
    scroll.value.removeEventListener("mousedown", mousedown)
    scroll.value.removeEventListener("mousewheel", mousewheel)
    removeEventListener("mouseup", mouseup)
    scroll.value.removeEventListener("mousemove", mousemove)
    removeEventListener("resize", update)
})
</script>

<template>
    <div class="horizontal_scroll">
        <div class="horizontal_scroll-row" ref="scroll">
            <slot></slot>
            <div class="horizontal_scroll-left" :class="{ show: !at_start }"></div>
            <div class="horizontal_scroll-right" :class="{ show: !at_end }"></div>
        </div>
    </div>
</template>