<script setup>
import { ref, onMounted, onUnmounted, nextTick } from 'vue'
import SymbolHey from './SymbolHey.vue'
import ModelCanvas from './ModelCanvas.vue'

const H_main = ref(null)
// const H_clip = ref(null)
const H_path = ref(null)
const H_content = ref(null)
const wrapper = ref(null)
const gap = ref(null)
const chara = ref(null)


function lerp(f, a, b) {
    return a * f + b * (1 - f)
}

const coverParallax = []
const vCoverParallax = {
    mounted(el, binding) {
        console.log('cover parallax mounted')
        el.coverParallaxObject = { el, binding }
        coverParallax.push(el.coverParallaxObject)
    },
    unmounted(el) {
        const index = coverParallax.indexOf(el.coverParallaxObject)
        if (index >= 0) coverParallax.splice(index, 1)
    }
}

let dynamic_factor = 0
function _update(animate = true) {
    const rect = H_main.value.getBoundingClientRect()
    const rect_gap = gap.value.getBoundingClientRect()

    // Original size of the clip path
    const clip_w = 1563
    const clip_h = 1134

    // Default Values (for mobile)
    let scrollY = 0
    let factor = 1
    let width = rect.width * 1.4
    let s1 = width / clip_w

    // Top left corner of the H
    let x = - rect.width * 0.5
    let y = (rect.height - clip_h * s1 - 120)

    const large_width = Math.max(innerWidth, innerHeight * 2) * 3


    let targetWidth = width
    let targetX = x
    let targetY = y

    if (innerWidth > 600) {
        targetWidth = Math.min(800, rect.width / 2)
        targetX = 60
    }

    if (animate) {
        scrollY = window.scrollY
        factor = (1 - 1 / (1 + (scrollY / (rect_gap.height + 1)) * 50))
        // Clip to 0~1 range
        factor = Math.min(1, Math.max(0, factor))
        dynamic_factor += (factor - dynamic_factor) * 0.2
        factor = dynamic_factor

        // target width of the final image (clipped)
        width = lerp(factor, targetWidth, large_width)
        s1 = width / clip_w
        x = lerp(factor, targetX, (rect.width - large_width) / 2)
        y = (rect.height - clip_h * s1) / 2


    }

    // Apply H transform
    H_path.value.style.transform = `translate(${x}px, ${y}px) scale(${s1}) `

    // Center of H
    const hcx = x + clip_w * s1 / 2
    const hcy = y + clip_h * s1 / 2

    // cover size mode for content
    const content_w = 1920
    const content_h = 1080

    const max_s2 = Math.max(innerWidth / content_w, innerHeight / content_h)
    const s2 = Math.min(max_s2, Math.max(clip_w / content_w, clip_h / content_h) * s1)

    // Top left corner of the content
    let cx = hcx - content_w * s2 / 2
    let cy = hcy - content_h * s2 / 2

    // Apply Content transform
    H_content.value.style.transform = `translate(${cx}px, ${cy}px) scale(${s2})`


    if (scrollY > rect_gap.height) {
        wrapper.value.style.position = 'absolute'
    } else {
        wrapper.value.style.position = 'fixed'
    }

    const parallax = -(scrollY - rect_gap.height / 2) / 10

    for (let obj of coverParallax) {
        obj.el.style.transform = `translate(0, ${parallax * obj.binding.value}px)`
    }
}


function update() {
    if (H_main.value == undefined) return
    _update()
    requestAnimationFrame(update)
}

onMounted(function () {
    update()
    // addEventListener("scroll", update)
    // addEventListener("resize", update)
})
onUnmounted(function () {
    // removeEventListener("scroll", update)
    // removeEventListener("resize", update)
})


</script>
<template>
    <div class="cover_page">
        <!-- Gap for scrolling (video full screen here) -->
        <div class="cover_page-gap" ref="gap">
        </div>

        <!-- Main content starts here -->
        <div class="layout-pagesize" v-pagesize>

            <!-- Wrapper for BG, stays still when scrolling the gap area -->
            <div class="cover_page-wrapper" ref="wrapper" v-pagesize>
                <div class="layout-container">

                    <!-- Background -->
                    <div class="cover_page-bg">
                        <!-- H shape -->
                        <div class="cover_page-bg_H" ref="H_main">
                            <div class="cover_page-bg_H-clip">
                                <div class="cover_page-bg_H-content" ref="H_content">
                                    <video style="width:100%; height:100%" src="@/assets/video/hiiio.mp4" autoplay loop muted></video>
                                </div>
                            </div>
                            <svg viewBox="0 0 1563 1134">
                                <clipPath id="clipPath_H" ref="H_path">
                                    <path d="M1480.33,0.917l-544,-0c-45.375,-0 -82.166,36.791 -82.166,82.166l-0,237.959c-0,40.25 -32.667,72.916 -72.917,72.916c-40.25,0 -72.917,-32.666 -72.917,-72.916l0,-237.959c0,-45.375 -36.791,-82.166 -82.166,-82.166l-544,-0c-45.375,-0 -82.167,36.791 -82.167,82.166l-0,964.834c-0,45.375 36.792,82.166 82.167,82.166l544,0c45.375,0 82.166,-36.791 82.166,-82.166l0,-237.959c0,-40.291 32.667,-72.916 72.917,-72.916c40.25,-0 72.917,32.625 72.917,72.916l-0,237.959c-0,45.375 36.791,82.166 82.166,82.166l544,0c45.375,0 82.167,-36.791 82.167,-82.166l-0,-964.792c-0,-45.417 -36.792,-82.208 -82.167,-82.208Z" style="fill-rule:nonzero;" />
                                </clipPath>
                            </svg>
                        </div>
                        <!-- Character -->
                        <div ref="chara">
                            <div class="cover_page-bg_chara" v-parallax="2">
                                <div class="cover_page-bg_chara-img">
                                    <ModelCanvas></ModelCanvas>
                                </div>
                            </div>
                        </div>

                        <div class="cover_page-bg_scroll">
                            SCROLL ▽
                        </div>

                        <div class="cover_page-container">
                            <div class="cover_page-side">
                                <div class="cover_page-side-title">
                                    <h1>ANIMATION,<br />VTUBER,<br />TVCM.
                                    </h1>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>


            <div class="cover_page-container">
                <div class="cover_page-content">
                    <div class="cover_page-side">
                        <div class="cover_page-side-intro">
                            <h2>WE ARE HIIIO!
                                <SymbolHey></SymbolHey>
                            </h2>
                            <p>HIIIO is a group of outstanding 3D
                                Generalists.<br />
                                We transform all types of images
                                into 3D
                                models and produce the best quality
                                works.
                            </p>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
