<script setup>

import {
    Scene, WebGLRenderer, PerspectiveCamera,
    Vector3,
    AnimationMixer, Clock, sRGBEncoding, NearestFilter
} from 'three'


import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader'

import gltfmodel from '@/assets/model/model.glb'
import { ref, onMounted } from 'vue'

import { useMouse } from './useMouse.js'
const { mouseX, mouseY } = useMouse()

const show = ref(true)
const threeContainer = ref(null)
const loaded = ref(false)

// Fix model interpolation
function fixObject(obj) {
    for (let c of obj.children) {
        fixObject(c)
    }

    if (obj.material) {
        if (obj.material.map) {
            let texture = obj.material.map
            texture.magFilter = NearestFilter
            texture.minFilter = NearestFilter
        }
    }
}


async function setup() {

    const element = threeContainer.value
    // Create Renderer
    const renderer = new WebGLRenderer({ alpha: true, antialias: true })
    renderer.outputEncoding = sRGBEncoding
    renderer.setSize(1300, 1950)
    element.appendChild(renderer.domElement)

    // Create Scene
    const scene = new Scene()

    // Create Camera
    let size = new Vector3()
    renderer.getSize(size)
    const camera = new PerspectiveCamera(45, size.x / size.y, 0.01, 100)

    const cameraCenter = new Vector3(-0.03, 0.8, 2.1)
    camera.position.copy(cameraCenter)


    // Load Model
    const loader = new GLTFLoader()

    let mixer = null


    const model = await new Promise(done => {

        loader.load(gltfmodel, function (gltf) {
            console.log(gltf)
            const model = gltf.scene
            done(model)
            // fixObject(gltf.scene)

            // mixer = new AnimationMixer(model)
            // mixer.clipAction(gltf.animations[0]).play()
            // renderer.domElement.classList.add("reveal-show")

        }, function (xhr) {
            console.log((xhr.loaded / xhr.total * 100) + '% loaded')
        }, function (error) {
            console.log('An error happened', error)
        })
    })

    scene.add(model)

    const clock = new Clock()
    let time = 0
    let rotationDecay = 1

    let mx = 0
    let my = 0
    function animate() {
        if (threeContainer.value)
            requestAnimationFrame(animate)



        threeContainer.value.style.transform = ''
        const rect = threeContainer.value.getBoundingClientRect()
        const s = rect.height / size.y
        threeContainer.value.style.transform = `scale(${s})`
        
        show.value = rect.y + rect.height / 3 > 0
        if (!show.value) return
        
        const y = rect.y/s
        camera.position.setY(cameraCenter.y + y / size.y * 2)
        camera.setViewOffset(size.x, size.y, 0, y, size.x, size.y)

        time += clock.getDelta()

        mx += ((mouseX.value / innerWidth - 0.5) - mx) * 0.1
        my += ((mouseY.value / innerHeight - 0.5) - my) * 0.1

        model.rotation.y = Math.sin(time) * 0.2 + rotationDecay * 6 + mx
        model.rotation.x = my * 0.1
        time = time % (Math.PI * 2)
        rotationDecay *= 0.95

        if (mixer) mixer.update(clock.getDelta())



        renderer.render(scene, camera)

    }
    loaded.value = true
    animate()
}


onMounted(function () {
    setup()
})




</script>

<template>
    <div class="model_canvas" :class="{ 'model_canvas-loaded': loaded && show }">
        <div class="model_canvas-container" ref="threeContainer">
        </div>
        <img src="@/assets/imgs/Charac_1.webp" alt="">
    </div>
</template>



<style lang="scss">
.model_canvas {
    z-index: 2;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;

    &-container {
        position: relative;
        width: 100%;
        height: 100%;


    }

    canvas {
        margin: auto;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        opacity: 0;
        transition: opacity 1s;
    }

    img {
        position: absolute;
        transform: translate(-50%, 0);
        top: 0;
        height: 100%;
        opacity: 1;
        transition: opacity 1s;
    }

    &-loaded {
        canvas {
            opacity: 1;
        }

        img {
            opacity: 0;
        }
    }
}
</style>